// 根据id获取图层
export const getLayerById=(layerid)=>{
  return window.$mapbox.getLayer(layerid)
}
// 根据id获取Source
export const getSourceById=(layerid)=>{
    return  window.$mapbox.getSource(layerid)
}
// 根据id删除图层
export const removeLayerAndSourceById=(layerid)=>{
    if(getLayerById(layerid)){
      window.$mapbox.removeLayer(layerid);
    }
    if(getSourceById(layerid)){
      window.$mapbox.removeSource(layerid);
    }
}
// 根据id隐藏图层
export const hideLayerById=(layerid)=>{
    if(getLayerById(layerid)){
        window.$mapbox.setLayoutProperty(layerid, "visibility", "none");
    }
}
// 根据id显示图层
export const showLayerById=(layerid)=>{
    if(getLayerById(layerid)){
        window.$mapbox.setLayoutProperty(layerid, "visibility", "visible");
    }
}
// 添加arcgis切片图层
export const addArcgisTileLayer=(url,layerid,visibility)=>{
    window.$mapbox.addSource(layerid, {
        type: 'raster',
        tiles: [url+'/tile/{z}/{y}/{x}'],
        tileSize: 256
    });
    window.$mapbox.addLayer({
        id: layerid, type: "raster", source: layerid,
        layout: {
          visibility: visibility
        }
    });
}
// 添加动态切片图层
export const addVectorTileLayer=(url,layerid)=>{
    window.$mapbox.addSource(layerid, { 
        type: 'vector',
        scheme: "tms",
        tiles: [url]
    });
    window.$mapbox.addLayer({
        id: layerid,
        type: 'fill',//"circle",
        source: layerid,
        "source-layer": "points",
        paint: {
            // 'circle-color': 'red',
            // 'circle-radius': 3,
            // 'circle-stroke-width': 1,
            // 'circle-stroke-color': 'white',  
            "fill-color":"red",
            "fill-opacity":0.6,
            "fill-outline-color":"black"
        }
    });
}
//添加arcgis地图服务
export const addArcgisImageLayer=(url,layerid)=>{
    window.$mapbox.addSource(layerid, { 
        type: 'raster',
        tiles: [url+"/export?dpi=96&transparent=true&format=png8&layers=&bbox={bbox-epsg-3857}&f=image&bboxSR=102100&imageSR=102100"],
        tileSize: 256
    });
    window.$mapbox.addLayer({
        id: layerid,
        type: "raster",
        source: layerid 
    });   
}
//添加features
export const addFeatureLayer=(layerid,features,imgurl,imgid)=>{
    window.$mapbox.loadImage(imgurl, function (error, image) {
        if (error) throw error;
        if(! window.$mapbox.hasImage(imgid)){
          window.$mapbox.addImage(imgid, image);
        }
        window.$mapbox.addSource(layerid, { 
            type: 'geojson',
            data: {
                type: 'FeatureCollection',
                features: features
            }
        });
       window.$mapbox.addLayer({
        id: layerid,
        type: 'symbol',//'circle',
        source: layerid,
        layout: {
            "icon-image": imgid,
            "icon-size": 0.8
        }
        });
    });
}