<template>
  <div class="container">
    <MapView></MapView>
    <MapTools></MapTools>
    <MapPoup></MapPoup>
     <div class="bnts" style="position: absolute;top: 0;">
     
      <!-- <button @click="addpoints">添加</button>
      <button @click="removepoints">删除</button>
      <button @click="addechartslayer">echartslayer添加</button>
      <button @click="removeechartslayer">echartslayer删除</button> -->
    </div>
  </div>
</template>
<script>
import MapView from "./MapView.vue";
import MapTools from "./MapTools.vue";
import MapPoup from "./MapPoup.vue";
import * as hxlayer from "../../maputils/hxlayer";
import {createEhartLayer} from '../../maputils/echartLayer' 
var echartlayertest;
export default {
  components: {
    MapView,
    MapTools,
    MapPoup
  },
   methods: {
    addpoints() {
       var features=[ 
      { type: 'Feature',geometry: {type: 'Point',coordinates: [108.011407938526289, 34.25879524975873]},properties:{name:"xxx",type:"12"}},
      { type: 'Feature',geometry: {type: 'Point',coordinates: [109.01207938526289, 35.26879524975873]},properties:{name:"xxx",type:"12"}},
      { type: 'Feature',geometry: {type: 'Point',coordinates: [109.23207938526289, 36.27879524975873]},properties:{name:"xxx",type:"12"}},
      { type: 'Feature',geometry: {type: 'Point',coordinates: [108.53207938526289, 34.27879524975873]},properties:{name:"xxx",type:"12"}},
      { type: 'Feature',geometry: {type: 'Point',coordinates: [108.75407938526289, 36.28879524975873]},properties:{name:"xxx",type:"12"}},
      ]
      hxlayer.addFeatureLayer("markspoints",features,require('@/assets/15.png'),"store-icon");
     
    },
    removepoints() {
       window.$mapbox.removeLayer("markspoints");
       window.$mapbox.removeSource("markspoints");
    },
    addechartslayer(){
      if(!echartlayertest){
         echartlayertest=createEhartLayer()
      }
     
    },
    removeechartslayer(){
       if(echartlayertest){
         echartlayertest.then(function(res){
          res.remove();
          echartlayertest=null;
         })
       
       }
    }
  }
};
</script>

<style>
.container {
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  background-color: antiquewhite;
}
</style>
