<template>
  <div id="popup" class="ol-popup">
    <div id="contentId"></div>
  </div>
</template>

<script>

import poup1 from "../../components/poup1";
import mapboxgl from 'mapbox-gl'
import { createApp, onMounted } from "vue";
export default {
  setup() {
    let mapPopup = null;
    onMounted(() => {
      addPopup();
    });
    const addPopup = () => {
      let container = document.getElementById("popup");
      window.$mapbox.on('click','markspoints', function (e) {   
          var coordinates = e.features[0].geometry.coordinates.slice();
          mapPopup = createApp(poup1);
          mapPopup.config.globalProperties.$pValue = e.features[0].properties;
          mapPopup.mount("#contentId");
          while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
            coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
          }
          window.$mapbox.flyTo({
             center: coordinates,
             zoom: 9,
              speed: 1
          });
         new mapboxgl.Popup().setLngLat(coordinates).setHTML(container.outerHTML).addTo(window.$mapbox);
      });
    
    };
  },
};
</script>

<style lang="css">
.ol-popup {
  min-width: 200px;
  min-height: 100px;
  border: 1px solid #eee;
  background: #fff;
}


</style>
